import React, { useState } from "react";
import axios from "axios";
import ImageModal from "./ImageModal"; //    
import "./JobOptionMobile.css";

const JobOptionMobile = ({ branch, worker, bussiness, date, status, id}) => {
  const [imageData, setImageData] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const apiUrl = import.meta.env.VITE_API_URL;

  const handleViewImage = async () => {
    try {
      const response = await axios.get(`${apiUrl}/manager/cleanings/${id}/image`);
      if (response.data.image) {
        setImageData(response.data.image);
        setShowModal(true);
      } else {
        alert("    ");
      }
    } catch (error) {
      console.error("  :", error);
      alert("  ");
    }
  };

  const handleDeleteCleaning = async () => {
    try {
        const confirmDelete = window.confirm("      ?");
        if (!confirmDelete) return;

        const response = await axios.delete(`${apiUrl}/manager/delete-cleaning/${id}`);

        if (response.status === 200) {
            alert("  !");
        } else {
            alert("  ");
        }
    } catch (error) {
        console.error("  :", error);
        alert("  ");
    }
};

  return (
    <div className="joboptionmobile">
      <div></div>
      <div>{date}</div>

      <div></div>
      <div>{bussiness}</div>

      <div></div>
      <div>{branch}</div>

      <div> </div>
      <div>{worker}</div>

      <div></div>
      <div>{status ? "" : " "}</div>

      {/*  */}
      <button className="full-span" onClick={handleViewImage}> </button>
      <button className="full-span" onClick={handleDeleteCleaning}> </button>

      {showModal && (
        <ImageModal
          image={imageData}
          onClose={() => setShowModal(false)}
        />
      )}
    </div>
  );

};



export default JobOptionMobile;
