import React, { useState } from "react";
import axios from "axios";
import ImageModal from "./ImageModal"; //    
import "./Search.css";

const Search = ({className = "",worker,status,branch,date,bussiness,id,}) => {
  const [imageData, setImageData] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const apiUrl = import.meta.env.VITE_API_URL;

  const handleViewImage = async () => {
    try {
      //   : GET /worker/cleanings/:id/image
      const response = await axios.get(`${apiUrl}/manager/cleanings/${id}/image`);
      if (response.data.image) {
        setImageData(response.data.image);
        setShowModal(true);
      } else {
        alert("    ");
      }
    } catch (error) {
      console.error("  :", error);
      alert("  ");
    }
  };
  const handleDeleteCleaning = async () => {
    try {
        const confirmDelete = window.confirm("      ?");
        if (!confirmDelete) return;
        const response = await axios.delete(`${apiUrl}/manager/delete-cleaning/${id}`);
        if (response.status === 200) {
            alert("  !");
        } else {
            alert("  ");
        }
    } catch (error) {
        console.error("  :", error);
        alert("  ");
    }
};
  return (
    <div className={`search ${className}`}>
      <div className="date">{date}</div>
      <div className="business">{bussiness}</div>
      <div className="branch">{branch}</div>
      <div className="worker">{worker}</div>
      <div className="status">
        {status ? "" : " "}
      </div>
      
      <button className="img-btn" onClick={handleViewImage}>
         
      </button>
      <button className="img-btn" onClick={handleDeleteCleaning} >
         
      </button>

      {showModal && (
        <ImageModal
          image={imageData} 
          onClose={() => setShowModal(false)}
        />
      )}
    </div>
  );
};

export default Search;
